.about {
  margin-top: 10rem;
}

.about-profile {
  margin-top: 4.8rem;

  display: flex;
  align-items: center;
  gap: 1.6rem;
}

.about-bio {
  max-width: 655px;
  margin-top: 2.4rem;
}

.about-break {
  display: none;
} 

/*

  MEDIA QUERY

*/

@media only screen and (min-width: 375px) {
  .about {
    margin-top: 12rem;
  }
}

@media only screen and (min-width: 520px) {
  
}

@media only screen and (min-width: 768px) {
  .about {
    margin-top: 14rem;
  }

  .about-bio {
    margin-top: 3.2rem;
  }

  .about-break {
    display: block;
  }
}

@media only screen and (min-width: 1024px) {
  .about-profile {
    margin-top: 6.4rem;
  }
}

@media only screen and (min-width: 1280px) {
  
}