.gists {
  margin-top: 5.6rem;
}

.link-no-decoration {
  text-decoration: none;

  display: flex;
  align-items: center;
}

.gist-list {
  margin-top: 3.2rem;

  display: grid;
  gap: 3.2rem;
}

.text-gradient-blue {
  color: rgb(30, 0, 255);
  background-image: linear-gradient(45deg, rgb(30, 0, 255) 0%, rgb(112, 43, 252) 80%, rgb(242, 59, 255) 160%, rgb(255, 112, 118) 240%, rgb(29, 29, 31) 320%);
  background-clip: text; 
  -webkit-background-clip: text; 
  -webkit-text-fill-color: transparent; 
}

.text-gradient-orangered {
  color: rgb(237, 24, 106);
  background-image: linear-gradient(90deg, rgb(237, 24, 106) 0%, rgb(255, 103, 0) 100%, rgb(255, 203, 57) 200%, rgb(29, 29, 31) 300%);
  background-clip: text; 
  -webkit-background-clip: text; 
  -webkit-text-fill-color: transparent; 
}

.link-title {
  padding-right: 1rem;
}


.gist-item {
  max-width: 655px;
}

.gist-description {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}